// import source from '../data/agami.json';
import { Vector } from 'p5';
import { leftBuffer, wid } from '../utils/config';
import { rankByStates, Accountant } from '../utils/utils';

const { TweenLite } = window;

export default function sketch(p) {
  let reesh = false;
  let myCluster,
    lastPressed,
    filters = {};
  const numC = {};
  let N = window.source.length;
  let role = null;
  let byStates = null,
    byPartnership;
  const Q = parseInt(Math.sqrt(N) + 2);
  const spacing = wid / Q;
  window.hoverDetails = null;

  class Cluster {
    constructor(data) {
      this.nodes = data.map((v, i) => new Node(v, i));
      this.premium = this.nodes
        .filter((n) => n.dna.Category !== 'Idea Prize')
        .sort((a, b) =>
          a.dna.Category > b.dna.Category
            ? 1
            : b.dna.Category > a.dna.Category
            ? -1
            : 0
        );
    }

    filteredNodes = (nds) => {
      if (Object.keys(filters).length === 0) return nds;
      const res = nds.filter((nd) => {
        return Object.entries(filters).every(([fil, filv]) => {
          if (!filv) return true;
          if (filv.length === 0) return true;
          if (!(fil in nd.dna)) return false;
          if (Array.isArray(nd.dna[fil])) {
            return nd.dna[fil].some((q) => filv.includes(q));
          } else {
            return filv.filter((o) => o).includes(nd.dna[fil]);
          }
        });
      });
      if (res.length === 0) {
        reesh = true;
        window.searchFound = 0;
      } else {
        reesh = false;
        window.searchFound = res.length;
      }
      return res;
    };

    genericClustering(key, num, ord) {
      const fNodes = this.filteredNodes(this.premium);
      byPartnership = Accountant(key, fNodes).reverse();
      if (ord) {
        const obj = Object.fromEntries(byPartnership);
        byPartnership = ord.map((k) => [k, obj[k]]);
      }
      numC[num] = byPartnership;
      byPartnership.forEach(([st, stco], sti) => {
        const mahState = fNodes.filter((n) => n.dna[key] == st);
        const Q = parseInt(Math.sqrt(mahState.length));
        const QQ = parseInt(p.width / 25) - 2;

        mahState.forEach((stt, stti) => {
          const YY = p.width / byPartnership.length;
          const YYY = p.height / byPartnership.length;

          TweenLite.to(stt.pos, 1, {
            x:
              byPartnership.length > 2 || key === 'Stage'
                ? p.width * 0.04 + 25 * (stti % QQ)
                : sti * YY + YY / 8 + 35 * (stti % Q),
            y:
              byPartnership.length > 2 || key === 'Stage'
                ? 40 + sti * YYY + 35 * parseInt(stti / QQ)
                : p.height * 0.3 + 35 * parseInt(stti / Q),
          });
          TweenLite.to(stt, 1, {
            r: 15,
            color: stt.homeColor,
          });
        });
      });
    }
    show() {
      this.filteredNodes(this.nodes).forEach((n) => n.show());
      if (role === '3' && byStates) {
        p.textAlign(p.RIGHT);
        p.fill(0);
        p.textSize(14);
        byStates.forEach(([st, stco], sti) => {
          p.text(st, 100, 20 + sti * 35);
        });
      }
      for (var i = 4; i < 20; i++) {
        if (parseInt(role) === i && numC[i]) {
          numC[i].forEach(([st, stco], sti) => {
            const Q = p.width / numC[i].length;
            const Xoff = sti * Q + Q / 2;
            const Yoff = sti * Q;
            p.fill(0);
            if (numC[i].length <= 2 && role !== '10') {
              p.textAlign(p.CENTER);
              p.text(st, Xoff, p.height * 0.2);
            } else {
              p.textAlign(p.LEFT);
              p.textSize(14);
              p.text(st, p.width * 0.04 - 10, Yoff + 20);
            }
          });
        }
      }
    }
    step14() {
      const fNodes = this.filteredNodes(this.nodes);
      fNodes.forEach((n, ni) => {
        TweenLite.to(n.pos, 1, {
          x: spacing * (ni % Q),
          y: spacing * parseInt(ni / Q),
        });
        TweenLite.to(n, 1, {
          r: 20,
          color: n.homeColor,
        });
      });
    }
    step13() {
      this.nodes
        .filter((n) => n.dna.Category === 'Idea Prize')
        .forEach((n) => {
          TweenLite.to(n.pos, 1, {
            y: -1000,
          });
        });
      this.genericClustering('Size', 13, [
        '0 to 2',
        '3 to 5',
        '6 to 10',
        '11 to 20',
        '21 or more',
      ]);
    }
    step12() {
      this.genericClustering('Founders', 12, [
        '1',
        '2',
        '3',
        '4',
        'More than 5',
      ]);
    }
    step11() {
      this.genericClustering('Duration', 11, [
        '0 to 2',
        '2 to 5',
        '5 to 10',
        '10 or more',
      ]);
    }
    step10() {
      this.genericClustering('Stage', 10);
    }
    step9() {
      this.genericClustering('Type', 9);
    }
    step8() {
      this.genericClustering('Gender', 8);
    }
    step7() {
      this.genericClustering('Background', 7);
    }
    step6() {
      this.genericClustering('Partnered', 6);
    }
    step5() {
      this.genericClustering('Child', 5);
    }
    step4() {
      this.genericClustering('SPartnered', 4);
    }
    step3() {
      const fNodes = this.filteredNodes(this.nodes);

      byStates = rankByStates(fNodes);

      //Hide Idea Prize
      fNodes
        .filter((n) => n.dna.Category === 'Idea Prize')
        .forEach((n) => {
          TweenLite.to(n.pos, 1, {
            y: -1000,
          });
        });

      byStates.forEach(([st, stco], sti) => {
        const mahState = fNodes.filter(
          (n) => n.dna.State === st && n.dna.Category !== 'Idea Prize'
        );
        mahState
          .sort((a, b) =>
            a.dna.Category > b.dna.Category
              ? 1
              : b.dna.Category > a.dna.Category
              ? -1
              : 0
          )
          .forEach((stt, stti) => {
            TweenLite.to(stt.pos, 1, {
              x: 120 + stti * 20,
              y: 20 + sti * 35 - 5,
            });
            TweenLite.to(stt, 1, {
              r: 15,
              color: stt.homeColor,
            });
          });
      });
    }
    step2() {
      const sizes = [128, 39, 84];
      const colors = [
        p.color(119, 73, 113),
        p.color(218, 110, 170),
        p.color(213, 221, 37),
      ];
      const positions = [
        { x: p.width / 2, y: p.height * 0.3 },
        { x: p.width * 0.32, y: p.height * 0.7 },
        { x: p.width * 0.7, y: p.height * 0.7 },
      ];
      const fNodes = this.filteredNodes(this.nodes);
      [
        'Idea Prize',
        'Industry Prize',
        'Shamnad Basheer Citizenship Prize',
      ].forEach((prize, prizeIndex) => {
        const myNodes = fNodes.filter((n) => n.dna.Category === prize);
        myNodes.forEach((n) => {
          TweenLite.to(n.pos, 1, positions[prizeIndex]);
          TweenLite.to(n, 2, {
            r: 2.4 * sizes[prizeIndex],
            color: colors[prizeIndex],
          });
        });
      });
    }
    default() {
      const fNodes = this.filteredNodes(this.nodes);
      fNodes.forEach((n) => {
        TweenLite.to(n.pos, 1, {
          x: spacing * (n.ind % Q),
          y: spacing * parseInt(n.ind / Q),
        });
        TweenLite.to(n, 2, { r: 20, color: p.color(14, 18, 56) });
      });
    }
  }

  class Node {
    constructor(data, ind) {
      this.dna = data;
      this.ind = ind;
      this.r = 20;
      this.color = p.color(14, 18, 56);

      if (data.Category === 'Industry Prize') {
        this.homeColor = p.color(218, 110, 170);
      } else if (data.Category === 'Shamnad Basheer Citizenship Prize') {
        this.homeColor = p.color(213, 221, 37);
      } else {
        this.homeColor = p.color(119, 73, 113);
      }

      this.pos = p.createVector(
        spacing * (ind % Q),
        spacing * parseInt(ind / Q)
      );
    }

    // update() {
    //   var distance = this.targ.dist(this.pos);
    //   var mappedDistance = p.map(distance, 100, 0, 1.5, 0.5);
    //   var diff = Vector.sub(this.targ, this.pos);
    //   diff.normalize();
    //   diff.mult(2 * mappedDistance);
    //   this.pos.add(diff);
    // }

    show() {
      p.fill(255, 0, 0);
      //p.ellipse(0, 0, 100, 100);
      //p.text(p.mouseX + ' ' + p.mouseY, p.width / 2, p.height - 20);
      const {
        pos: { x, y },
        r,
        dna: { Category, Name },
      } = this;
      p.fill(this.color);
      if (
        parseInt(role) >= 3 &&
        (role === '14' || Category !== 'Idea Prize') &&
        p.abs(x - p.mouseX) <= r * 0.7 &&
        p.abs(y - p.mouseY) <= r * 0.7
      ) {
        p.fill(0);
        // p.cursor('crosshair');
        p.textAlign(p.RIGHT);
        p.textSize(18);
        p.text(Name, p.width - 10, p.height - 20);
        p.fill(0, 255, 0);
        //p.cursor('')
        if (p.mouseIsPressed) {
          window.registerClick(this.dna);
        }
      }
      p.ellipse(x, y, r, r);
    }
  }

  p.setup = function () {
    myCluster = new Cluster(window.source);
    p.createCanvas(wid, wid);
    p.noStroke();
    // p.ellipseMode(p.CENTER);
    p.rectMode(p.CENTER);
    p.textAlign(p.CENTER);
    p.textFont('Muli');
  };

  p.myCustomRedrawAccordingToNewPropsHandler = function (props) {
    role = props.role;
    filters = props.filters;
    if (Object.keys(filters).length == 0) reesh = false;
    if (role === '1' && myCluster) {
      myCluster.default();
    }
    if (role === '2' && myCluster) {
      myCluster.step2();
    }
    if (role === '3' && myCluster) {
      myCluster.step3();
    }
    if (role === '4' && myCluster) {
      myCluster.step4();
    }
    if (role === '5' && myCluster) {
      myCluster.step5();
    }
    if (role === '6' && myCluster) {
      myCluster.step6();
    }
    if (role === '7' && myCluster) {
      myCluster.step7();
    }
    if (role === '8' && myCluster) {
      myCluster.step8();
    }
    if (role === '9' && myCluster) {
      myCluster.step9();
    }
    if (role === '10' && myCluster) {
      myCluster.step10();
    }
    if (role === '11' && myCluster) {
      myCluster.step11();
    }
    if (role === '12' && myCluster) {
      myCluster.step12();
    }
    if (role === '13' && myCluster) {
      myCluster.step13();
    }
    if (role === '14' && myCluster) {
      myCluster.step14();
    }
  };

  p.draw = function () {
    p.background(255);
    p.translate(leftBuffer, leftBuffer);
    // p.rect(0, 0, 100, 100)

    // p.rect(0, 0, source.length, source.length);

    p.textAlign(p.CENTER);
    if (reesh) {
      p.fill(0);
      p.text(
        'Sorry! No teams found with these filters',
        p.width / 2,
        p.height * 0.4
      );
    }
    myCluster.show();
  };
}
