import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { isMobile, universalCategorizer } from '../utils/utils';
const Wrapper = styled.div`
  width: ${!isMobile ? 30 : 100}vw;
  background: white;
  position: fixed;
  z-index: 9999;
  right: ${(props) => (props.visible ? 0 : `-${!isMobile ? 30 : 100}vw`)};
  height: ${!isMobile ? 100 : 50}vh;
  top: ${!isMobile ? 0 : 50}vh;
  bottom: 0;
  box-shadow: -8px 0 10px -6px #00000022;
  overflow: scroll;
  transition: 0.2s all ease-out;
`;

const Cell = styled.div`
  border-bottom: 1px solid #eee;

  .hdr {
    cursor: pointer;
    padding: 25px;
    transition: 0.1s ease-in all;
    &:hover {
      background: #30cf8c;
      color: white;
    }
  }
`;

const Options = styled.div`
  padding: 15px;
  display: flex;
  flex-wrap: wrap;
`;

const LabelWrapper = styled.div`
  padding: 12px;
  background: #e2e2e2;
  border-radius: 6px;
  margin: 12px;
  color: black;
  transition: 0.1s ease-in all;
  cursor: pointer;
  &:hover {
    background: #f1f1f1;
  }
`;

const Label = ({ lbl, onClick, style }) => (
  <LabelWrapper style={style} onClick={onClick}>
    {lbl}
  </LabelWrapper>
);

const imp = ['Category', 'Stage', 'Perspective', 'Elements'];

const legible = {
  Category: 'Category',
  Child: 'Part of Larger Org?',
  Type: 'Profit/Non Profit',
  Duration: 'How old is the project?',
  State: 'State',
  Size: 'Team Size',
  Founders: 'Number of Founders',
  Stage: 'Progress Stage',
  Partnered: 'Partnered with others?',
  SPartnered: 'Partnered with State?',
  Funding: 'Received Funding?',
  Vertical: 'Agami Vertical',
  Tools: 'Tools Used',
  Perspective: 'Design Perspective',
  Gender: 'Gender of Team Members',
  Age: 'Age of Team Members',
  Background: 'Law/Non law',
  Background2: 'Non law Backgrounds',
  Elements: 'Elements of Justice',
  Needs: 'Resources Needed',
};

export default function Sidebar({ visible, applyFilters }) {
  const cats = universalCategorizer();
  const [cellOpen, setCellOpen] = useState({});
  const [more, setMore] = useState(false);
  useEffect(() => {
    applyFilters(cellOpen);
  }, [cellOpen]);

  useEffect(() => {
    window.document.body.style.overflow = visible ? 'hidden' : 'scroll';
    if (!visible) {
      setCellOpen({});
    }
  }, [visible]);
  const openCell = (c) => {
    if (c in cellOpen && cellOpen[c] !== undefined) {
      setCellOpen({ ...cellOpen, [c]: undefined });
    } else {
      setCellOpen({ ...cellOpen, [c]: [] });
    }
  };
  const toggleLabel = (lbl, catg) => {
    const newState = { ...cellOpen };
    if (!newState[catg].includes(lbl)) {
      newState[catg].push(lbl);
    } else {
      newState[catg] = newState[catg].filter((l) => l != lbl);
    }
    setCellOpen(newState);
  };
  return (
    <Wrapper visible={visible}>
      <Cell
        style={{
          fontWeight: 'bold',
          borderBottom: '1px solid grey',
          marginBottom: 20,
          fontSize: 24,
          padding: 25,
        }}
      >
        Apply Filter
      </Cell>
      {cats
        .filter(([cat, lb]) => (more ? true : imp.includes(cat)))
        .map(([cat, catLabels]) => (
          <>
            <Cell>
              <div
                className='hdr'
                onClick={() => openCell(cat)}
                style={{ fontWeight: cellOpen === cat ? 'bold' : 'normal' }}
              >
                {legible[cat]}
              </div>
              {cat in cellOpen && cellOpen[cat] !== undefined && (
                <Options>
                  {catLabels.map((lbl) => (
                    <Label
                      onClick={() => toggleLabel(lbl, cat)}
                      lbl={lbl}
                      style={{
                        backgroundColor:
                          cellOpen[cat] && cellOpen[cat].includes(lbl)
                            ? '#30cf8c'
                            : '#efefef',
                        color:
                          cellOpen[cat] && cellOpen[cat].includes(lbl)
                            ? '#fff'
                            : '#222',
                      }}
                    />
                  ))}
                </Options>
              )}
            </Cell>
          </>
        ))}
      <Cell
        style={{
          fontWeight: 'bold',
          marginBottom: 20,
          fontSize: 14,
          padding: 25,
          textTransform: 'uppercase',
          textAlign: 'center',
          cursor: 'pointer',
        }}
        onClick={() => setMore(!more)}
      >
        Show {more ? 'Less' : 'More'} Filters
      </Cell>
    </Wrapper>
  );
}
