const Container = styled.div`
  display: flex;
  flex-direction: ${isMobile ? 'column' : 'row'};
  justify-content: space-around;
  background: white;
`;

const Graphic = styled.div`
  flex-basis: 70%;
  height: 300px;
  position: sticky;
  top: ${isMobile ? 0 : window.innerHeight * 0.13}px;
  z-index: 2001;
  text-align: center;
  background: white;
`;

const Scroller = styled.div`
  flex-basis: ${isMobile ? 0 : 30}%;
  text-align: ${isMobile ? 'center' : ''};
  background: #30cf8c;
  color: white;
  scroll-snap-type: y mandatory;
`;

const Hdr = styled.div`
  padding: 0 30px;
  text-align: left;
  transform: ${`translateY(${isMobile ? 50 : -50}%)`};

  .top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .title {
      font-weight: 800;
      font-size: 32px;
    }
  }
  img {
    height: 40px;
    margin-top: 25px;
  }
`;

const Step = styled.div`
  width: 100%;
  height: ${isMobile ? 'auto' : '100vh'};
  font-size: 25px;
  padding-left: 0px;
  margin-top: 20px;
  box-sizing: border-box;
  border-bottom: 1px dashed white;
  padding: 10vh 10% 10vh 10%;
  text-align: center;
  /* overflow: hidden; */
  z-index: 1;
  opacity: 0.8;
`;

const titles = {
  3: 'Top States By Applications Received',
  4: 'Partnership with Government?',
  5: 'Part of a Larger Organization?',
  6: 'Partnership with others in the field?',
  7: 'Team Background',
  8: 'Gender Composition',
  9: 'Type Distribution',
  10: 'Progress Stage',
  11: 'How old is the project?',
  12: 'Founding Team Size',
  13: 'Team Size',
  14: 'Playground',
};

function App() {
  const [city, setCity] = useState('');
  const [filters, applyFilters] = useState({});
  const [dimensions, setDimension] = useState({ w: 0, h: 0 });
  const [sidebarVisible, setSidebarVisible] = useState(false);
  const [modalVisible, setModalVisible] = useState(false);
  const [company, setCompany] = useState(null);
  const [dataLoaded, setDataLoaded] = useState(false);
  const [blocks, setBlocks] = useState([]);

  useEffect(() => {
    const initialize = async () => {
      if (lscache.get('agami')) {
        setDataLoaded(true);
      } else {
        await scrapeData();
        setDataLoaded(true);
      }

      window.onload = function () {
        if (window.localStorage.getItem('includeIdea')) {
          window.includeIdea =
            window.localStorage.getItem('includeIdea') === 'Yes';
        } else {
          window.includeIdea = false;
        }
      };

      window.onbeforeunload = function () {
        window.scrollTo(0, 0);
      };
      window.registerClick = (e) => {
        setCompany(e);
        setModalVisible(true);
      };
    };
    initialize();
  }, []);

  useEffect(() => {
    if (dataLoaded) {
      const data = JSON.parse(lscache.get('agami'));
      setTimeout(() => {
        const source = cleanData(data);
        window.source = source;
        setBlocks(generateBlocks());
      }, 1000);
    }
  }, [dataLoaded]);

  const onResize = () => {
    let screenWidth = window.innerWidth;
    let screenHeight = window.innerHeight;

    if (screenWidth > 768) {
      screenWidth = screenWidth * 0.55;
    } else {
      screenWidth = screenWidth * 0.9;
    }
    setDimension({ w: screenWidth, h: screenHeight });
  };

  const onStepEnter = (city, { currentPosition, previousPosition }) => {
    setCity(city);
    // const el = document.querySelector(`#waypoint-${city}`);
    // el.style.backgroundColor = '#85e085';
    // el.style.border = '2px solid green';
  };

  const onStepExit = (city, { currentPosition, previousPosition }) => {
    if (city === 'nyc' && currentPosition === 'below') {
      setCity('');
    }
    // const el = document.querySelector(`#waypoint-${city}`);
    // el.style.backgroundColor = 'whitesmoke';
    // el.style.border = '2px solid grey';
  };

  if (!dataLoaded || !window.source)
    return (
      <div
        style={{
          width: '100vw',
          height: '100vh',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <img src={loader} style={{ width: 80 }} />
      </div>
    );

  return (
    <div>
      {modalVisible && (
        <Modal company={company} close={() => setModalVisible(false)} />
      )}
      <Sidebar visible={sidebarVisible} applyFilters={applyFilters} />
      {/* <Trigger /> */}
      {/* <Header>A fucking intro</Header> */}
      <Container>
        {!isMobile && (
          <Graphic>
            {parseInt(city) >= 3 && (
              <Hdr>
                <div className='top'>
                  <div className='title'>{titles[city]}</div>
                  <Button
                    sidebarVisible={sidebarVisible}
                    onClick={() => setSidebarVisible(!sidebarVisible)}
                  />
                </div>
                <img src={legend} />
              </Hdr>
            )}
            <Visualization
              width={dimensions.w}
              height={dimensions.h}
              role={city}
              filters={filters}
            />
          </Graphic>
        )}
        <Scroller>
          {blocks.map((block) => {
            const city = block.id;
            return (
              <Waypoint
                onEnter={(obj) => onStepEnter(city, obj)}
                onLeave={(obj) => onStepExit(city, obj)}
                scrollableAncestor={window}
                topOffset={city === '1' ? '30%' : '60%'}
                bottomOffset={city === '1' ? '69%' : '39%'}
                key={city}
              >
                <Step id={`waypoint-${city}`} key={city}>
                  <div>{block.jsx}</div>
                </Step>
              </Waypoint>
            );
          })}
        </Scroller>
      </Container>
    </div>
  );
}

export default App;
