import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 5vh;
`;

const Ball = styled.div`
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin: 8px;
`;
const discern = (n) => {
  if (n.Category === 'Industry Prize') {
    return 'rgb(218, 110, 170)';
  } else if (n.Category === 'Shamnad Basheer Citizenship Prize') {
    return 'rgb(213, 221, 37)';
  } else if (n.Mission.includes('Empowering')) {
    return 'rgb(213, 221, 37)';
  } else {
    return 'rgb(218, 110, 170)';
  }
};

const z = (n) => {
  if (n.Category != 'Idea Prize') return n.Category;
  if (n.Mission.includes('Empowering')) {
    return 'Shamnad Basheer Citizenship Prize';
  } else {
    return 'Industry Prize';
  }
};
export default function MGraph({ keyo, team }) {
  const mahPeeps = window.source
    .filter(
      (t) =>
        t[keyo] == team.toString() &&
        (window.includeIdea ? true : t.Category != 'Idea Prize')
    )
    .sort((a, b) => (z(a) > z(b) ? 1 : z(b) > z(a) ? -1 : 0));
  if (!keyo) return null;
  return (
    <>
      <Wrapper>
        {mahPeeps.map((p) => (
          <Ball
            onClick={() => window.registerClick(p)}
            style={{
              background: discern(p),
            }}
          />
        ))}
      </Wrapper>
      <div
        style={{
          marginBottom: '5vh',
          display: 'flex',
          marginTop: '5vh',
          justifyContent: 'space-around',
          fontSize: 12,
        }}
      >
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <Ball
            style={{
              background: 'rgb(218, 110, 170)',
              width: 12,
              height: 12,
            }}
          >
            {' '}
          </Ball>
          <span>Industry Prize</span>
        </div>
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <Ball
            style={{
              background: 'rgb(213, 221, 37)',
              width: 12,
              height: 12,
            }}
          >
            {' '}
          </Ball>
          <span>Citizenship Prize</span>
        </div>
      </div>
    </>
  );
}
