export const N = 251;
const n = Math.ceil(Math.sqrt(N));
export const W = max(window.innerWidth, window.innerHeight);
const isMobile = window.innerWidth < window.innerHeight;

export const wid = isMobile
  ? window.innerWidth * 0.95
  : window.innerHeight * 0.8;

function max(v1, v2) {
  return v1 > v2 ? v1 : v2;
}

function min(v1, v2) {
  return v1 > v2 ? v2 : v1;
}
export const circSpacing = min(W / (n + 1), 20000);
export const circRadius = min(circSpacing * 0.3, 20);
export const leftBuffer = W === window.innerHeight ? 10 : 10;
