export const isMobile = window.innerWidth < window.innerHeight;

export const Accountant = (key, dnaified) => {
  const counter = {};
  if (!dnaified) {
    window.source
      .filter((team) =>
        window.includeIdea ? true : team.Category != 'Idea Prize'
      )
      .forEach((team) => {
        if (team[key] in counter) {
          counter[team[key]] += 1;
        } else {
          counter[team[key]] = 1;
        }
      });
  } else {
    dnaified
      .filter((team) => team.dna.Category !== 'Idea Prize')
      .forEach((team) => {
        if (team.dna[key] in counter) {
          counter[team.dna[key]] += 1;
        } else {
          counter[team.dna[key]] = 1;
        }
      });
  }
  const j = Object.entries(counter)
    .filter(([k, v]) => k)
    .sort(
      key === 'State'
        ? (a, b) => b[1] - a[1]
        : (a, b) => (a > b ? 1 : b > a ? -1 : 0)
    );
  return !isMobile ? j : { j, key };
};

const Presenter = (arro, isGrid) => {
  let arr = 'j' in arro ? arro.j : arro;
  const key = arro.key;
  console.log(arro, arr);
  const total = arr.reduce((b, a) => b + a[1], 0);
  return (
    <div
      style={{
        display: isGrid ? 'grid' : 'block',
        gridTemplateColumns: isGrid && !isMobile ? 'auto auto' : 'none',
      }}
    >
      {arr
        .filter(([team, t]) => team != 'undefined' && !!team)
        .map(([team, teamCount]) => (
          <div>
            <h2 style={{ fontSize: isGrid ? 60 : 70, margin: 10 }}>
              {parseInt((teamCount * 100) / total) < 1
                ? parseInt((teamCount * 1000) / total) / 10
                : parseInt((teamCount * 100) / total)}
              %
            </h2>
            <div style={{ marginBottom: isMobile ? 20 : 60 }}>{team}</div>
            <MGraph keyo={key} team={team} />
          </div>
        ))}
    </div>
  );
};

