export const renderCategoryInfo = () => {
  const counter = {};
  window.source.forEach((team) => {
    if (team.Category in counter) {
      counter[team.Category] += 1;
    } else {
      counter[team.Category] = 1;
    }
  });
  return (
    <div>
      <div>{Presenter(Object.entries(counter))}</div>
      {isMobile && (
        <div>
          <input
            style={{ fontSize: 24 }}
            id='includeIdea'
            checked={window.includeIdea}
            type='checkbox'
            onChange={(e) => {
              console.log(e.target.checked);
              window.localStorage.setItem(
                'includeIdea',
                e.target.checked ? 'Yes' : 'No'
              );
              window.location.reload();
            }}
          />
          <span style={{ marginLeft: 14 }}>
            Include Idea Prize entries in analysis
          </span>
        </div>
      )}
    </div>
  );
};

