const arrFields = [
  'Areas',
  'Funding',
  'Tags',
  'Vertical',
  'Tools',
  'Perspective',
  'Gender',
  'Age',
  'Background',
  'Background2',
  'Elements',
  'Needs',
];

export const universalCategorizer = () => {
  if (!window.source) return [];
  const labels = {};
  const src = window.source.filter((c) => c.Category != 'Idea Prize');
  Object.keys(src[0])
    .filter((k) => !arrFields.includes(k))
    .forEach((key) => {
      const s = Accountant(key);
      const lbls = 'j' in s ? s.j : s;
      labels[key] = lbls.map((v) => v[0]);
    });
  arrFields.forEach((arF) => {
    const vals = src
      .filter((x) => arF in x)
      .reduce((b, a) => {
        return a[arF] ? [...b, ...a[arF].filter((v) => !b.includes(v))] : b;
      }, []);
    labels[arF] = vals;
  });
  return Object.entries(labels).filter((k) => k[1].length < 25);
};

export function scrapeData() {
  return new Promise(function (resolve, reject) {
    // Get a base ID for an instance of art gallery example
    console.log('scaping....');
    const entries = [];
    var base = new Airtable({ apiKey: 'keyTEID634X0Qm6BN' }).base(
      'appPA1YIFLlTlTkSH'
    );
    return base('Analysis _ WIP')
      .select()
      .eachPage(
        function page(records, fetchNextPage) {
          records.forEach(function (record) {
            entries.push(record.fields);
          });
          fetchNextPage();
        },
        function done(error) {
          lscache.set('agami', JSON.stringify(entries), 1440);
          console.log('DPNEEEE', entries);
          resolve(entries);
        }
      );
  });
}

const format = (arr) => {
  return arr && arr.map((t) => t.trim()).sort();
};

export const cleanData = (data) => {
  return data.map((e) => {
    const newEntry = {
      id: e['Application ID'],
      Name: e['Initiative Name'],
      //   Category:
      //     e['Category'] !== 'Idea Prize'
      //       ? e['Category']
      //       : e['Which of the following is your idea targeted towards?'] ===
      //         'Empowering citizens to access justice'
      //       ? 'IP-C'
      //       : e['Tammanna Category Switch'] === 'Idea Prize - Citizenship'
      //       ? 'IP-C'
      //       : 'IP-I',
      Category: e['Category'],
      Mission: e['Which of the following is your idea targeted towards?'],
      Child: e['Is your initiative part of a larger organisation?'],
      Parent: e['Name of the larger organisation'],
      Type: e['What type of initiative are you?'],
      Duration: e['How old is your initiative?'],
      State: e['Where is your initiative primarily based?'],
      City: e['Name of the City / Town / Village'],
      Size:
        e['How many team members are working on your initiative full time?'],
      Founders: e['How many founders does your initiative have?'],
      Stage: e['Which stage would you say your initiative is at?'],
      Partnered:
        e['Have you partnered with others in the field of law and justice?'],
      SPartnered:
        e['Have you partnered with any State institutions for your work?'],
      Website: e['Website'],
      S1: e['Which State institition have you partnered with and how?'],
      S2: e['One - Name of the State'],

      //Arrays
      Areas: format(e['Pick one or more areas that your work fits under']),
      Funding: format(
        e['Have you raised any kind of funding for your initiative?']
      ),
      Tags: format(e['Pick one or more areas that your work fits under']),
      Vertical: format(e['Agami Vertical/Horizontal Link']),
      Tools: format(e['Tools (the way in which it is being done)']),
      Perspective: format(e['Perspective & Design Principles']),
      Gender: format(e['Gender']),
      Age: format(e['Age']),
      Background: format(e['Background']),
      Background2: format(e['Background 2']),
      Elements: format(e['Elements of Justice']),
      Needs: [
        e['First - Which resource are you most in need of? (First Priority)'],
        e['Second - Which resource are you most in need of? (Second Priority)'],
        e['Third - Which resource are you most in need of? (Third Priority)'],
      ],
    };
    return newEntry;
  });
};

