import React from 'react';
import styled from 'styled-components';
import { isMobile } from '../utils/utils';
const Wrapper = styled.div`
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 10001;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ModalContainer = styled.div`
  width: ${!isMobile ? 50 : 80}vw;
  height: ${!isMobile ? 50 : 60}vh;
  background: white;
  padding: 25px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  overflow: scroll;
  .spacer {
    flex: 1;
  }
  .modal-body {
    margin-top: 20px;
    display: ${!isMobile ? 'flex' : 'block'};
    flex-wrap: wrap;
    .item {
      padding: ${isMobile ? '12px 0' : '12px 25px'};
    }
  }
`;

const Button = styled.div`
  padding: 15px 30px;
  min-width: 100px;
  background: #30cf8c;
  color: white;
  text-align: center;
  cursor: pointer;
  border-radius: 50px;
  display: inline-block;
`;
const keysToShow = [
  'Name',
  'Category',
  'Parent',
  'Type',
  'Duration',
  'Size',
  'Founders',
  'Stage',
  'S1',
];

export default function Modal({ company, close }) {
  const { State, City, Website } = company;
  return (
    <Wrapper>
      <ModalContainer>
        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
          <div>
            <span style={{ fontWeight: 'bold', fontSize: 18 }}>
              {company.Name}
            </span>
            <span style={{ fontSize: 11 }}>
              , {City}, {State}
            </span>
          </div>
          <div style={{ cursor: 'pointer', fontSize: 28 }} onClick={close}>
            x
          </div>
        </div>
        <div className='modal-body'>
          {Object.entries(company)
            .filter(([k, v]) => v && keysToShow.includes(k))
            .map(([k, v]) => {
              return (
                <div className='item'>
                  <div style={{ fontWeight: 800 }}>
                    {k != 'S1' ? k : 'State Partnership'}
                  </div>
                  <div>{v}</div>
                </div>
              );
            })}
        </div>
        <div className='spacer' />
        <div className='modal-footer'>
          {Website && (
            <Button
              onClick={(e) => {
                e.preventDefault();
                window.location.href = Website;
              }}
            >
              Visit
            </Button>
          )}
        </div>
      </ModalContainer>
    </Wrapper>
  );
}
